import React from 'react';
import Article from './Article';

export default function NewsDisplay(props){
    const articles=props.articles;
    let id = 0;
    if (articles.length) {
        return (
            <div className='NewsDisplay'>
                {articles.map(article => {
                    let img = article.urlToImage;
                    if (!article.urlToImage) {
                        img = 'data:image/png;base64,';
                    }
                    id += 1;
                    return <Article key={`${article.source.names}${id}`} id={id} name={article.source.name} date={article.publishedAt} image={img} title={article.title} description={article.description} content={article.content} />
                })}
            </div>
        )
    } else {
        return (
            <div className='NoArticlesFound'>
                <h1>There are no articles matching your request</h1>
            </div>
        )
    }
}
